





























import cmdIcon from "./cmd-icon.vue"
export default {
  name: 'cmd-nav-bar',

  components: {
    cmdIcon
  },

  props: {
    /**
     * 固定到页面顶部
     */
    fixed: {
      type: Boolean,
      default: true
    },
    /**
     * 文字图标颜色
     */
    fontColor: {
      type: String,
      default: ''
    },
    /**
     * 导航栏背景颜色
     */
    backgroundColor: {
      type: String,
      default: ''
    },
    /**
     * 导航栏标题
     */
    title: {
      type: String,
      default: ''
    },
    /**
     * 导航栏左侧返回按钮，默认点击返回上层
     */
    back: {
      type: Boolean,
      default: false
    },
    /**
     * 左侧文字可同显返回按钮
     */
    leftText: {
      type: String,
      default: ''
    },
    /**
     * 左侧显示标题，不可显示左侧文字图标
     */
    leftTitle: {
      type: String,
      default: ''
    },
    /**
     * 右侧文字
     */
    rightText: {
      type: String,
      default: ''
    },
    /**
     * 右侧文字颜色
     */
    rightColor: {
      type: String,
      default: ''
    },
    /**
     * 图标一，不可与返回按钮,左侧标题同显
     */
    iconOne: {
      type: String,
      default: ''
    },
    /**
     * 图标二
     */
    iconTwo: {
      type: String,
      default: ''
    },
    /**
     * 图标三，须显有图标二
     */
    iconThree: {
      type: String,
      default: ''
    },
    /**
     * 图标四，不可与右侧文字同显
     */
    iconFour: {
      type: String,
      default: ''
    }
  },

  computed: {
    /**
     * 设置标题图标颜色
     */
    setFontColor() {
      let fontColor = '#000';
      if (this.fontColor != '') {
        fontColor = this.fontColor;
      }
      return fontColor;
    },
    /**
     * 设置背景颜色
     */
    setBackgroundColor() {
      let backgroundColor = '#fff';
      if (this.backgroundColor != '') {
        backgroundColor = `background: ${this.backgroundColor};`;
      }
      return backgroundColor;
    }
  },

  methods: {
    /**
     * 图标一点击事件
     */
    $_iconOneClick() {
      if (this.back) {
        uni.navigateBack()
      } else {
        this.$emit("iconOne");
      }
    },
    /**
     * 图标二点击事件
     */
    $_iconTwoClick() {
      this.$emit("iconTwo");
    },
    /**
     * 图标三点击事件
     */
    $_iconThreeClick() {
      this.$emit("iconThree");
    },
    /**
     * 图标四点击事件
     */
    $_iconFourClick() {
      this.$emit("iconFour");
    },
    /**
     * 左侧文字点击事件
     */
    $_leftTextClick() {
      this.$emit("leftText");
    },
    /**
     * 右侧文字点击事件
     */
    $_rightTextClick() {
      this.$emit("rightText");
    }
  }

}
